import vars from './variables';

vars.$homeSlider.owlCarousel({
	nav: true,
	items: 1,
	margin: 30,
	dots: false,
	navText: [
		'<span class="left-arrow"></span>',
		'<span class="right-arrow"></span>'
	],
	responsive: {
		// breakpoint from 0 up
		0: {
			items: 1,
			margin: 0
		},
		// breakpoint from 768 up
		768: {
			items: 1,
			margin: 30
		}
	}
});



